.gallery{
    min-height: 100vh;
}
.gallery .row{
    min-height: 100vh;
    
}
.gal1{
    background: 
    /* top, black */ 
    linear-gradient(
      rgba(2, 0, 0, 0.5), 
      rgba(2, 0, 0, 0.5)
    ),
    /* bottom, image */
    url("../images/gal1.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border:6px solid black!important;
}
.gal1:hover{
    background: 
    /* top, transparent red */ 
    linear-gradient(
      rgba(255, 0, 0, 0.45), 
      rgba(255, 0, 0, 0.45)
    ),
    /* bottom, image */
    url("../images/gal1.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    transition: 0.3s;

}


