.homeVideoBanner{
    height: 100vh;
    width: 100%;
   
}
.homeVideoBanner video{
    height: 100vh;
    width: 100%;
    object-fit: cover;
}
.content{
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:white;
    line-height: 0.3rem;
    z-index: 1;
    border-bottom: 6px solid black;
    
    

}

.content h1{
    font-size: 3.45rem;
    
   
    
    
}

.btnclass{
    padding: 0.5rem 1rem;
    border-bottom: 3px solid #8f242a;
    border-radius: 10px;
    background-color: #c1272d;
    -webkit-transition: background-color 500ms ease;
    transition: background-color 500ms ease;
    
    color: #fff;
    font-weight: 600;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.btnclass:hover{
    background-color: #521114;
    border-color: black;
    transition: 0.3s;
        
}
.blackbackground{
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: rgb(12, 1, 1);
    opacity: 0.4;
    

}

/*mediaqueries*/
@media  (min-width: 0) and (max-width: 575.98px) {
    .content h1{
        font-size: 2.5rem;
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
        
       
        
        
    }
    .content p{
        font-size:1.7rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
       text-align: center;
       line-height: 3.5rem;
        
       
        
        
    }
    .btnclass{
        letter-spacing: normal;
    padding: 1rem 0.5rem;
    font-size: 1rem;

margin:auto;}

}

@media  (min-width: 576px) and (max-width: 767.98px) {
    .content h1{
        font-size: 3.45rem;
        
       
        
        
    }
    .btnclass{
        letter-spacing: normal;
    padding: 1rem 0.5rem;
    font-size: 1rem;

margin:auto;}

 }

 

 




