#contact, #contact .row{
   min-height: 100vh;
    
   
}

#contact input,#contact textarea{
    width: 160%;
    background-color:grey;
    border-radius: 10px;
}
#contact input::placeholder{
    color: white;
}
 #contact textarea::placeholder{
    color: white;
    padding-left: 0.5rem;
    padding-top: 0.5rem;
    font-size: 1.2rem;
}
#contact input:focus,#contact textarea:focus{
    border-color: rgb(143, 36, 42)!important;
     
    box-shadow: 0 0 0 0.25rem rgb(143 36 42 / 25%)!important;
    outline: none;
    
}
#contact input:focus::placeholder,#contact textarea:focus::placeholder{
    color:#8f242a;
    outline: none;
}



.contactformbtn{
    padding: 0.5rem 1rem;
    border-bottom: 3px solid #8f242a;
    border-radius: 10px;
    width: fit-content;
    background-color: #c1272d;
    -webkit-transition: background-color 500ms ease;
    transition: background-color 500ms ease;
    
    color: #fff;
    font-weight: 600;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-decoration: none;
}

.contactformbtn:hover{
    background-color: #521114;
    border-color: black;
    transition: 0.3s;
    color: #fff;
        
}

/*mediaquery*/
@media (min-width: 0px) and (max-width: 767.98px) { 
    #contact .row{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #contact .row .col-6{
        width:100%;
       
    }
    #contact .row .col-6 .row .col-8{
        width: 100%;
       
        padding: 2rem 0.5rem;
        
       
    }
    #contact .row .col-6 .row .col-8 h4{
        font-size: 2.5rem;
        margin-bottom: 2rem;
       
    }
    #contact .row .col-6 .row .col-8 h6{
        font-size:1.7rem;
        line-height: 2.9rem!important;
       margin-top: 0.5rem;
       margin-bottom: 0.5rem;
       
       
    }
    #contact .row .col-6 .row .col-8 h6 img{
       height: 2.5rem;
      
    }
   
   
   
    #contact .row .col-6 .row .col-4{
        width: 100%;
       
        padding: 2rem 0.5rem;
    }
    #contact .row .col-6 .row .col-4 h4{
        font-size: 2.5rem;
        margin-bottom: 2rem;
        

    }
    #contact .row .col-6 .row .col-4 h4 img{
        height: 2.5rem;
        margin-bottom: 2rem;
        
        

    }
    .mqfollowusicon{
        margin-left: 6rem!important;
    }


    .mqcol-6{
       
        
        display: flex!important;
        flex-direction: column!important;
        justify-content: center!important;
        align-items: center!important;
        padding-left: 0!important;
        padding-right: 0!important;
        border-top: 10px double black;
        border-bottom: 3px solid black;
        padding-top: 3rem;
        padding-bottom: 3rem;
        
    }
    .mqcol-6 h4{
        font-size: 2.5rem;
    }
    .mqcol-6 form{
      
       width: 100%;
       padding: 2rem 2rem!important;
    
    }
    #contact input,#contact textarea{
        width: 100%;
        background-color:grey;
        border-radius: 10px;
        margin-top: 0.9rem;
        margin-bottom: 0.9rem;
    }

    #contact input::placeholder{
        color: white;
        font-size: 1.7rem;
        
    }
     #contact textarea::placeholder{
        color: white;
        padding-left: 0.5rem;
        padding-top: 0.5rem;
        font-size: 1.7rem;
    }



    




}
   
 