.navbar-nav{
    
    width: 100%;
    justify-content: end;
   
}
.dropdown-item:hover{
    background-color: #800b17;
    cursor: pointer;
    transition: 0.3s;
    

}
.ahover{
    color: grey!important;
}
.ahover:hover{
    color: white!important;
    

}
  
/*media queries*/
@media (max-width: 991.98px) {
    .navmq{
        background-color: red!important;
        padding-left: 0!important;
        padding-right: 0!important;
    }
    
    .nav-link{
        text-align: center;
    }
    .navmq1{
        
        flex-direction: column;
    }
    .navbar-brand{
        font-size: 2rem;
       
        
    }
    .navbar-nav{
        font-size: 1.5rem;
    }
    .dropdown-menu li{
            text-align: center;
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;
            font-size: 1.5rem;
    }
    
    }



    