.TrainingSection{
    
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.TrainingSection .row .col-6{
    min-height:70vh;
    
    border-top: 0;
    border-left: 3px solid black;
    border-right: 3px solid black ;
    border-bottom: 6px solid black;
    
    color: white;
   /* background-image: url("../images/personaltrainingimg.webp");*/
    

}
.img1{
   
    background: 
    /* top, black */ 
    linear-gradient(
      rgba(2, 0, 0, 0.5), 
      rgba(2, 0, 0, 0.5)
    ),
    /* bottom, image */
    url("../images/professionaltrainingimg.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
}
.img1:hover{
   
    background: 
    /* top, transparent red */ 
    linear-gradient(
      rgba(255, 0, 0, 0.45), 
      rgba(255, 0, 0, 0.45)
    ),
    /* bottom, image */
    url("../images/professionaltrainingimg.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    transition: 0.3s;

 }

.img2{
   background: 
    /* top, transparent red */ 
    linear-gradient(
      rgba(2, 0, 0, 0.5), 
      rgba(2, 0, 0, 0.5)
    ),
    /* bottom, image */
    url("../images/personaltrainingimg.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    

}
.img2:hover{
    background: 
    /* top, transparent red */ 
    linear-gradient(
      rgba(255, 0, 0, 0.45), 
      rgba(255, 0, 0, 0.45)
    ),
    /* bottom, image */
    url("../images/personaltrainingimg.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    transition: 0.3s;
    
    

}

.TrainingSection .row .col-6 h6{
    opacity: 0.5;
    z-index: 999;
}


@media  (min-width: 0) and (max-width: 575.98px){
  
  .img1,.img2{
   width: 100%;
    height: 100vh;
    border: none!important;
    border-top: 3px solid black!important;
    border-bottom: 6px solid black!important;
    
    
  }
  .TrainingSection h1{
    font-size: 2.5rem!important;
    margin-top: 1rem;
    margin-bottom: 1rem;

  }
  .TrainingSection h6{
    font-size: 1.7rem!important;
   

  }
  .TrainingSection a{
    font-size: 4rem!important;

  }
  
}

