.powerportion{
    background-color: #39414f;

}
.powerportion .row .col-3, .powerportion .row .col-9{
    min-height: 70vh;
    background-color: #39414f;
}
.powerportion .row .col-3 img{
    height:6rem;

}
.powerportion .row .col-9 h6{
   opacity: 0.5;
   line-height: 0.3rem;


}

.powerportionbtn{
    padding: 0.5rem 1rem;
    border-bottom: 3px solid #8f242a;
    border-radius: 10px;
    width: fit-content;
    background-color: #c1272d;
    -webkit-transition: background-color 500ms ease;
    transition: background-color 500ms ease;
    
    color: #fff;
    font-weight: 600;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-decoration: none;
}

.powerportionbtn:hover{
    background-color: #521114;
    border-color: black;
    transition: 0.3s;
    color: #fff;
        
}


/*mediaqueries*/
@media  (min-width: 0) and (max-width: 575.98px){
    .powerportion .row{
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    }
    .powerportion .row .col-3{
        
        background-color: #39414f;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        text-align: center;
        min-height: 20vh;
        padding: 5vh 0;
       
        
    }
     .powerportion .row .col-9{
        
        background-color: #39414f;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100vw;
        text-align: center;
        min-height: 30vh;
        font-size: larger;
        padding-bottom: 10vh;
       
        
    }
    
    .powerportion .row .col-3 img{
        height:8rem;
    
    }
    .powerportion{
       border-bottom: 6px solid black;
    
    }
}