.Testinomails, .Testinomails .container,.Testinomails .container .card-group{
    min-height: 70vh;
    
    
}

.Testinomails .card-img-top{
    height: 3rem;
}


/*mediaqueries*/
@media (max-width: 575.98px) {
    .Testinomails, .Testinomails .container,.Testinomails .container .card-group{
        height: 100vh;

        
    } 
    .card{
        margin-top: auto!important;
        margin-bottom: auto!important;
        height: 33.33vh; 
        font-size: larger!important;   
    }

 }