.aboutgym{
    background-color: #39414f;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 6px solid black;
}
.greyp{
    color: grey;
}

/*media query*/
@media (min-width: 0px) and (max-width: 575.98px)  {
    .aboutgym{
        height: 100vh;
        text-align: center;
        
        font-size: larger;
        
        
    }
    .aboutgym h1{
        font-size: 2.5rem;
    }
    .greyp{
        font-size: 1.7rem;
        
    }
    
    
}
